import facebook_grey from "@root/assets/images/facebook-grey.png";
import instagram_grey from "@root/assets/images/instagram-grey.png";
import linkedin_grey from "@root/assets/images/linkedin-grey.png";
import twitter_grey from "@root/assets/images/twitter-grey.png";

import facebook_white from "@root/assets/images/facebook-white.png";
import instagram_white from "@root/assets/images/instagram-white.png";
import linkedin_white from "@root/assets/images/linkedin-white.png";
import twitter_white from "@root/assets/images/twitter-white.png";

export default class SocialPanel extends React.Component {
  render() {
    return (
      <div className="panel">
        <a href="http://linkedin.com">
          <div className="link">
            <img src={this.props.grey ? linkedin_grey : linkedin_white} />
          </div>
        </a>
        <a href="http://twitter.com">
          <div className="link">
            <img src={this.props.grey ? twitter_grey : twitter_white} />
          </div>
        </a>
        <a href="http://facebook.com">
          <div className="link">
            <img src={this.props.grey ? facebook_grey : facebook_white} />
          </div>
        </a>
        <a href="http://instagram.com">
          <div className="link">
            <img src={this.props.grey ? instagram_grey : instagram_white} />
          </div>
        </a>
        <style jsx>{`
          .panel {
            display: flex;
            flex-flow: row nowrap;

            height: 30px;
          }

          a {
            height: 100%;
            width: 24px;
            padding: 0 4px;
          }

          .link {
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            height: 100%;
          }

          img {
            height: 100%;
            width: 100%;
            object-fit: contain;
          }
        `}</style>
      </div>
    )
  }
}



